import freePopup from '@/components/free-ui/free-popup.vue';
export default {
	components: {
		freePopup
	},
	props: {
		title: {
			type: String,
			default: "提示"
		},
	},
	data() {
		return {
			callback: false
		}
	},
	methods: {
		// 显示
		show(callback = false){
			this.callback = callback
			this.$refs.confirm.show()
		},
		// 取消
		cancel() {
			this.$refs.confirm.hide()
		},
		// 确定
		confirm(){
			if(typeof this.callback === 'function'){
				this.callback(()=>{
					this.cancel()
				})
			}
		}
	},
}
